.content-container{
    width:90%;
    margin: 0 auto;
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
      > div{
        flex: 0 0 48%;
        margin:5% 0;
        padding: 0 1%;
    }
}

@media (max-width: 600px) {
    .content-container{
      flex-direction: column;
    }
}